@import 'font';
@import 'variables';
@import 'global';

header {
  width: 100%;
  min-height: 60px;
  padding: 1em 0;
  background-color: var(--dark-bg);
  color: var(--bright);
}

.header--fixed {
  position: fixed;
  top: 0;
  background-color: #fff;
  color: var(--dark);
  box-shadow: 1px 1px 8px 1px var(--dark);
  .menu {
    &__icon {
      background-color: var(--dark);
      opacity: .8;
      &.active {
        height: .65em;
        background-color: transparent;
        background-image: url(../images/assets/menu-arrow--mobile.svg);
        background-repeat: no-repeat;
        background-size: cover;
        &::before,
        &::after {
          display: none;
        }
      }
      &::after,
      &::before {
        background-color: var(--dark);
      }
    }
  }
}

.logo {
  font-family: 'Raleway ExtraBold';
  font-size: clamp(1.2rem, 6vw, 1.875rem);
}

.menu {
  position: relative;
  width: 30%;
  &__title {
    font-size: clamp(1rem, 5.5vw, 1.5rem);
  }
  &__icon {
    width: 1.2em;
    height: .25em;
    background-color: var(--bright);
    &.active {
      height: .65em;
      background-color: transparent;
      background-image: url(../images/assets/menu-arrow.svg);
      background-repeat: no-repeat;
      background-size: cover;
      &::before,
      &::after {
        display: none;
      }
    }
    &::after,
    &::before {
      content: '';
      position: absolute;
      width: 1.2em;
      height: .25em;
      background-color: var(--bright);
    }
    &::after {
      top: 0;
    }
    &::before {
      bottom: .0625em;
    }
  }
}

.hero {
  min-height: 100vh;
  color: var(--bright);
  background-color: var(--dark-bg);
  &__nav-assist {
    display: none;
  }
  h1 {
    font-family: 'Raleway ExtraBold';
    font-size: clamp(2.5rem, 13vw, 3rem);
  }
  &__cite {
    margin-top: 1.875em;
    text-transform: initial;
    p {
      font-family: 'Raleway Regular';
      line-height: 1.5;
    }
    figcaption {
      margin-top: 1em;
      text-align: right;
      font-family: 'Raleway Light Italic';
      font-size: clamp(.75rem, 4vw, 1.125rem);
    }
  }
}

#menu {
  position: fixed;
  top: 9%;
  right: 2%;
  width: 40%;
  background-color: #fff;
  box-shadow: 1px 1px 8px 1px var(--dark);
  transition: opacity var(--fadeOut);
  opacity: 0;
  .logo {
    display: none;
  }
  .flex-container {
    flex-direction: column;
    padding: .25em 0;
    li {
      padding: .5em 0;
    }
  }
  &.active {
    opacity: 1;
  }
}

h2 {
  margin: 1.5em 0 .5em 0;
  font-family: 'Raleway ExtraBold';
  font-size: clamp(1.2rem, 6vw, 1.875rem);
}

.subheading {
  font-size: clamp(1rem, 5vw, 1.125rem);
  text-transform: initial;
}

.about {
  img {
    margin-top: .75em;
    margin-bottom: .75em;
  }
  &__desc {
    font-size: clamp(.75rem, 3vw, .875rem);
    text-transform: initial;
  }
}

.stats {
  margin-top: 1.5em;
  padding: 2em 0;
  background-image: url(../images/assets/stats-bg.jpg);
  background-repeat: no-repeat;
  background-position: 35% 40%;
  background-attachment: fixed;
  color: var(--bright);
  .flex-container {
    flex-direction: column;
    .flex-item {
      width: 65%;
    }
  }
  &__desc {
    margin: 0;
    font-size: clamp(1rem, 2vw, 2rem);
  }
  &__point {
    font-size: clamp(3rem, 16vw, 4rem);
    margin-bottom: .25em;
  }
}

.products {
  display: block;
  margin: 1.75em 0;
  article + article {
    margin-top: 3em;
  }
  h3 {
    font-family: 'Raleway Bold';
    letter-spacing: 3px;
  }
  &__price {
    display: block;
    margin: .25em 0 .5em 0;
    font-size: clamp(1.5rem, 8vw, 1.875rem);
    letter-spacing: 3.75px;
  }
  &__link {
    font-size: clamp(1rem, 5vw, 1.125rem);
    text-transform: initial;
    text-decoration: underline;
    text-underline-offset: 2px;
    line-height: normal;
    letter-spacing: 2.25px;
    &:hover {
      text-decoration: none;
    }
  }
}

.staff {
  padding: .01em 0;
  background-color: var(--bright);
  .flex-container {
    display: block;
    margin: 1.75em 0;
  }
  figure + figure {
    margin-top: 3em;
  }
  .member__name {
    display: block;
    margin-top: .5em;
    font-family: 'Raleway Black';
    font-size: clamp(1.2rem, 6vw, 1.875rem);
  }
  .member__skill {
    display: block;
    margin-top: .5em;
    margin-left: 0;
  }
}

.feed {
  text-align: center;
  .flex-container {
    margin: 1.75em 0;
  }
  h4 {
    margin-bottom: 1.75em;
    font-family: 'Raleway ExtraBold';
    font-size: clamp(1rem, 5.5vw, 1.5rem);
    a:hover {
      text-decoration: underline;
      text-underline-offset: 2px;
    }
  }
}

footer {
  min-height: 900px;
  padding: 1.75em 0;
  background-color: var(--dark-bg);
  color: var(--bright);
  .wrapper--inner {
    text-align: left;
  }
  .logo {
    text-align: center;
    margin-bottom: 3em;
  }
  .flex-item {
    margin-top: 1.5em;
  }
  h5 {
    margin-bottom: .5em;
    font-family: 'Raleway Black';
    font-size: clamp(1rem, 5vw, 1.125rem);
    letter-spacing: 2.25px;
  }
  ul > li > a {
    display: block;
    padding: .25em 0;
    font-size: clamp(.9rem, 4.5vw, 1rem);
    transition: var(--fadeOut);
    &:hover {
      opacity: .3;
    }
  }
  .social-btn {
    display: block;
    width: 30px;
    height: 30px;
    font-size: 0;
    background-repeat: no-repeat;
    background-size: contain;
    transition: var(--fadeOut);
    &:hover {
      opacity: .3;
    }
    &.fb {
      background-image: url(../images/assets/facebook.webp);
    }
    &.linkn {
      background-image: url(../images/assets/linked-in.webp);
    }
    &.insta {
      background-image: url(../images/assets/instagram.webp);
    }
    &.tw {
      background-image: url(../images/assets/twitter.webp);
    }
  }
  img {
    filter: grayscale(1);
    transition: var(--fadeOut);
    cursor: pointer;
    &:hover {
      filter: none;
    }
  }
}

@media only screen and (min-width: 600px) {
  header {
    position: absolute;
    top: 0;
    min-height: 68px;
    background-color: transparent;
  }

  .menu {
    width: 16%;
    &__icon {
      width: 1.75em;
      &::after {
        width: 1.75em;
        top: .25em;
      }
      &::before {
        width: 1.75em;
        bottom: .20em;
      }
    }
  }

  .hero {
    text-align: right;
    background-image: url('../images/assets/hero-image.webp');
    background-repeat: no-repeat;
    background-position: 40% 30%;
    &__cite {
      width: 42%;
      margin-left: auto;
    }
    .wrapper {
      position: relative;
      z-index: 1;
      width: 100%;
      margin: 0;
      padding: 2.5em;
      &::before {
        content: '';
        position: absolute;
        inset: 0;
        z-index: -1;
        background-color: var(--dark);
        opacity: .4;
      }
    }
  }

  #menu {
    position: static;
    width: 100%;
    opacity: 1;
    .wrapper.flex-container {
      padding: 1em 0;
      .flex-container {
        width: 100%;
        flex-direction: row;
        flex-wrap: nowrap;
      }
    }
  }

  .stats {
    .flex-container {
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: flex-start;
    }
  }

  footer {
    min-height: 661px;
    .wrapper--inner {
      justify-content: space-around;
    }
  }
}

@media only screen and (min-width: 900px) {
  .menu {
    width: 12%;
  }

  .hero {
    min-height: 80vh;
    background-position-y: 0%;
  }

  .products {
    display: flex;
    article:first-child {
      margin-top: 3em;
    }
  }

  .staff {
    .flex-container {
      display: flex;
    }
    figure + figure {
      margin-top: 0;
    }
  }

  .feed {
    .flex-container {
      justify-content: center;
      img {
        margin: 0;
      }
    }
  }

  footer {
    min-height: 452px;
    .wrapper--inner {
      align-items: flex-start;
      justify-content: space-between;
    }
  }
}

@media only screen and (min-width: 1200px) {
  .menu {
    width: 7.25%;
    &__icon {
      transition: all var(--fadeOut);
    }
    &:hover,
    &:focus {
      .menu__icon {
        height: 1em;
        background-color: transparent;
        background-image: url(../images/assets/menu-arrow.svg);
        background-repeat: no-repeat;
        background-size: cover;
        &::before,
        &::after {
          display: none;
        }
      }
    }
  }

  #menu {
    .wrapper.flex-container {
      flex-direction: row;
      flex-wrap: nowrap;
    }
    .logo {
      display: block;
      margin-right: 7em;
    }
  }

  .hero {
    min-height: 100vh;
    background-size: cover;
    background-position-y: 45%;
    &__cite {
      width: 25%;
    }
    .wrapper {
      position: static;
      width: 98%;
      max-width: 1540px;
      margin: 0 auto;
      margin-top: 9em;
      padding: 0;
      &::before {
        display: none;
      }
    }
    &__nav-assist {
      position: fixed;
      z-index: 1;
      display: block;
      bottom: 5%;
      left: calc(50% - 45px);
      animation-name: mouseMove;
      animation-duration: 2s;
      animation-iteration-count: infinite;
      animation-direction: alternate;
      animation-timing-function: linear;
      filter: drop-shadow(2px 2px 1px var(--dark));
    }
  }

  @keyframes mouseMove {
    from {
      transform: translateY(0);
    }
    to {
      transform: translateY(10px);
    }
  }

  .about {
    &__desc {
      text-align: left;
    }
  }

  .stats {
    .flex-container {
      .flex-item:nth-child(2) {
        .stats__desc {
          width: 75%;
          margin: 0 auto;
        }
      }
      .flex-item:last-child {
        .stats__desc {
          width: 50%;
          margin: 0 auto;
        }
      }
    }
  }

  .staff {
    .flex-container {
      justify-content: space-around;
    }
  }

  .feed {
    img {
      transition: transform var(--fadeOut);
      &:hover {
        transform: scale(.9);
      }
    }
  }

  footer {
    .wrapper--inner {
      justify-content: space-around;
    }
  }
}

@media only screen and (min-width: 1900px) {
  .hero {
    background-size: unset;
    background-position: 0 15%;
  }
}